.landing-background {
    /* background-image: url('../images/map-of-nyc2.png') no-repeat center center fixed; */
    background-image: url(/static/media/background-with-buildings-pinned.59734d13.png) no-repeat center center fixed;
    
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    position: absolute;
    object-fit: cover;
    z-index: -1;
    min-height: 100%;
    min-width: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;

    /* -webkit-filter: saturate(400%);
    filter: saturate(400%); */
}

